/* VectorGraphPage.css - Matching site theme */

.vector-graph-page {
  padding: 20px;
  max-width: 1200px;
  margin: 0 auto;
  min-height: 100vh;
  box-sizing: border-box;
  background-color: white;
}

.vector-graph-header {
  margin: 0 0 20px 0;
  padding: 0;
  border: none;
}

.vector-graph-header h1 {
  margin: 0 0 10px 0;
  padding: 0;
  border: none;
  font-size: 2rem;
  font-weight: 700;
  color: #2d3748;
}

.vector-graph-header .subtitle {
  font-size: 1rem;
  color: #4a5568;
  line-height: 1.5;
  margin: 0;
}

.vector-graph-controls {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 20px;
  margin: 20px 0;
  padding: 12px 15px;
  background-color: #f7fafc;
  border-radius: 8px;
  border: 1px solid #e2e8f0;
}

.vector-graph-controls.pca-components-row {
  grid-template-columns: 1fr 1fr 1fr;
  margin-top: 0;
}

.control-group {
  display: flex;
  flex-direction: column;
  gap: 6px;
  min-width: 200px;
  flex: 1;
}

.stat-value-large {
  font-size: 18px;
  font-weight: 700;
  color: #2D3C50;
  line-height: 1;
  margin: 0;
  padding: 4px 0;
}

.pca-component {
  min-width: auto;
}

.pca-description {
  font-size: 12px;
  color: #2d3748;
  font-weight: 500;
  line-height: 1.3;
  margin: 0;
  padding: 2px 0;
}

.pca-feature {
  display: inline;
  transition: opacity 0.2s ease;
}

.control-group label {
  font-weight: 500;
  color: #2d3748;
  font-size: 13px;
  margin: 0;
}

.control-select {
  font-size: 14px;
  padding: 6px 12px;
  padding-right: 32px;
  border: 1px solid #2D3C50;
  border-radius: 4px;
  background: #2D3C50 url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 24 24' fill='white'%3e%3cpath d='M7 10l5 5 5-5z'/%3e%3c/svg%3e") no-repeat right 8px center / 20px;
  color: #ffffff;
  cursor: pointer;
  transition: background-color 0.2s ease;
  appearance: none;
  -webkit-appearance: none;
  -moz-appearance: none;
  box-shadow: none;
  height: auto;
}

.control-select:hover,
.control-select:focus {
  background-color: #5b7dab;
  border-color: #5b7dab;
  background: #5b7dab url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 24 24' fill='white'%3e%3cpath d='M7 10l5 5 5-5z'/%3e%3c/svg%3e") no-repeat right 8px center / 20px;
  outline: none;
  box-shadow: 0 0 0 2px rgba(45, 60, 80, 0.2);
}

.control-slider {
  width: 100%;
  height: 6px;
  border-radius: 3px;
  background: #e2e8f0;
  outline: none;
  appearance: none;
  -webkit-appearance: none;
  -moz-appearance: none;
}

.control-slider::-webkit-slider-thumb {
  appearance: none;
  -webkit-appearance: none;
  width: 18px;
  height: 18px;
  border-radius: 50%;
  background: #2D3C50;
  cursor: pointer;
  transition: background 0.2s;
}

.control-slider::-webkit-slider-thumb:hover {
  background: #5b7dab;
}

.control-slider::-moz-range-thumb {
  width: 18px;
  height: 18px;
  border-radius: 50%;
  background: #2D3C50;
  cursor: pointer;
  border: none;
  transition: background 0.2s;
}

.control-slider::-moz-range-thumb:hover {
  background: #5b7dab;
}

.slider-labels {
  display: flex;
  justify-content: space-between;
  font-size: 12px;
  color: #718096;
  margin-top: 4px;
}

.stats-display {
  display: flex;
  flex-direction: row;
  gap: 20px;
  flex-wrap: wrap;
  margin: 0;
  align-items: center;
}

.stat-item {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 8px;
}

.stat-label {
  font-size: 13px;
  color: #2d3748;
  font-weight: 500;
  margin: 0;
  white-space: nowrap;
}

.stat-value {
  font-size: 14px;
  font-weight: 700;
  color: #2D3C50;
  line-height: 1;
  margin: 0;
}

.vector-graph-content {
  margin: 20px 0 20px 0;
  background-color: #f7fafc;
  border-radius: 8px;
  border: 1px solid #e2e8f0;
  padding: 1rem;
  min-height: 600px;
}

.vector-graph-footer {
  margin: 20px 0;
  padding: 20px;
  background-color: #f7fafc;
  border-radius: 8px;
  border: 1px solid #e2e8f0;
}

.info-box h3 {
  margin: 0 0 15px 0;
  color: #2d3748;
  font-size: 1.25rem;
  font-weight: 700;
}

.info-box p {
  line-height: 1.6;
  color: #4a5568;
  margin-bottom: 10px;
}

.info-box .version-info {
  font-size: 0.875rem;
  color: #718096;
  font-style: italic;
  margin-bottom: 0;
}

.loading-container,
.error-container {
  text-align: center;
  padding: 4rem 2rem;
  background-color: #f7fafc;
  border-radius: 8px;
  border: 1px solid #e2e8f0;
}

.loading-container h2,
.error-container h2 {
  color: #2d3748;
  margin-bottom: 1rem;
  font-size: 1.5rem;
  font-weight: 700;
}

.error-container p {
  color: #e53e3e;
  margin: 0.5rem 0;
}

/* 3D Graph Styles */
.vector-graph-3d-container {
  position: relative;
  width: 100%;
  height: 600px;
  border-radius: 8px;
  overflow: hidden;
  background: #1a1a1a;
}

.vector-graph-empty {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 600px;
  color: #718096;
  text-align: center;
  padding: 2rem;
}

.vector-graph-empty p {
  margin: 0.5rem 0;
  font-size: 1rem;
  color: #4a5568;
}

.player-label {
  background: rgba(0, 0, 0, 0.85);
  color: white;
  padding: 0.5rem 0.75rem;
  border-radius: 6px;
  font-size: 0.85rem;
  pointer-events: none;
  white-space: nowrap;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.3);
}

.player-name {
  font-weight: 600;
  margin-bottom: 0.25rem;
}

.player-sets {
  font-size: 0.75rem;
  opacity: 0.9;
}

.vector-graph-info {
  position: absolute;
  top: 1rem;
  right: 1rem;
  background: rgba(255, 255, 255, 0.95);
  border-radius: 8px;
  padding: 1rem;
  min-width: 250px;
  max-width: 300px;
  min-height: 200px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.2);
  z-index: 10;
  max-height: calc(100% - 2rem);
  overflow-y: auto;
  border: 1px solid #e2e8f0;
}

.info-box-toggle {
  position: absolute;
  top: 0.75rem;
  right: 0.75rem;
  background: transparent;
  border: none;
  font-size: 1.5rem;
  color: #4a5568;
  cursor: pointer;
  padding: 0;
  width: 24px;
  height: 24px;
  display: flex;
  align-items: center;
  justify-content: center;
  line-height: 1;
  transition: color 0.2s ease;
  z-index: 11;
}

.info-box-toggle:hover {
  color: #2d3748;
}

.info-box-show-button {
  position: absolute;
  top: 1rem;
  right: 1rem;
  background: rgba(255, 255, 255, 0.95);
  border: 1px solid #e2e8f0;
  border-radius: 8px;
  font-size: 1.5rem;
  color: #4a5568;
  cursor: pointer;
  padding: 0.5rem 0.75rem;
  width: 40px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.2);
  transition: all 0.2s ease;
  z-index: 10;
}

.info-box-show-button:hover {
  background: rgba(255, 255, 255, 1);
  color: #2d3748;
  box-shadow: 0 6px 8px rgba(0, 0, 0, 0.3);
}

.info-section {
  margin-bottom: 1rem;
  border-bottom: 1px solid #e2e8f0;
}

.info-section:last-child {
  border-bottom: none;
  margin-bottom: 0;
  padding-bottom: 0;
}

.info-section-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  cursor: pointer;
  user-select: none;
  padding: 0.5rem 0;
  padding-right: 2rem;
  transition: background-color 0.2s ease;
  border-radius: 4px;
  margin: 0 0 0.5rem 0;
}

.info-section-header:hover {
  background-color: rgba(0, 0, 0, 0.05);
}

.info-section-header h4 {
  margin: 0;
  flex: 1;
  pointer-events: none;
}

.collapse-icon {
  font-size: 0.85rem;
  color: #718096;
  transition: transform 0.2s ease;
  margin-left: 0.5rem;
  pointer-events: none;
  display: inline-block;
  min-width: 12px;
}

.info-section.collapsed .info-section-header {
  margin-bottom: 0;
}

.info-section h3 {
  margin: 0 0 0.5rem 0;
  font-size: 1.1rem;
  color: #2d3748;
  font-weight: 700;
}

.info-section h4 {
  margin: 0;
  font-size: 0.95rem;
  color: #4a5568;
  font-weight: 600;
}

.info-section p {
  margin: 0.25rem 0;
  font-size: 0.85rem;
  color: #4a5568;
  line-height: 1.4;
}

.info-section ul {
  margin: 0.5rem 0;
  padding-left: 1.25rem;
  font-size: 0.85rem;
  color: #4a5568;
}

.info-section li {
  margin: 0.25rem 0;
  line-height: 1.4;
}

.hovered-player-info {
  background: #e6f2ff;
  padding: 0.75rem;
  border-radius: 6px;
  margin-top: 0.5rem;
  border: 1px solid #CFFFFF;
}

.player-info-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 0.5rem;
}

.player-info-header h4 {
  margin: 0;
  flex: 1;
}

.selected-badge {
  background: #4ecdc4;
  color: white;
  padding: 0.25rem 0.5rem;
  border-radius: 4px;
  font-size: 0.75rem;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.cluster-info {
  margin-top: 0.5rem;
  padding-top: 0.5rem;
  border-top: 1px solid rgba(0, 0, 0, 0.1);
}

.cluster-color-indicator {
  display: inline-block;
  width: 12px;
  height: 12px;
  border-radius: 50%;
  margin-left: 0.5rem;
  vertical-align: middle;
  border: 1px solid rgba(0, 0, 0, 0.2);
}

.similarity-info {
  margin-top: 0.5rem;
}

.archetype-description {
  font-size: 0.8rem;
  color: #718096;
  font-style: italic;
  margin: 0.25rem 0 0 0;
}

.hovered-player-info h4 {
  margin: 0 0 0.5rem 0;
  color: #2D3C50;
  font-size: 1rem;
  font-weight: 700;
}

.hovered-player-info p {
  margin: 0.25rem 0;
  font-size: 0.85rem;
  color: #4a5568;
}

.note {
  font-style: italic;
  color: #718096;
  font-size: 0.8rem !important;
}

/* Responsive design */
@media (max-width: 768px) {
  .vector-graph-page {
    padding: 1rem;
  }

  .vector-graph-header h1 {
    font-size: 1.75rem;
  }

  .vector-graph-controls {
    flex-direction: column;
    gap: 12px;
    padding: 10px 12px;
  }

  .control-group {
    width: 100%;
  }

  .vector-graph-content {
    min-height: 400px;
  }

  .vector-graph-3d-container {
    height: 400px;
  }

  .vector-graph-info {
    position: relative;
    top: auto;
    right: auto;
    max-width: 100%;
    margin-top: 1rem;
    max-height: none;
  }

  .vector-graph-empty {
    height: 400px;
  }

  .vector-graph-controls {
    grid-template-columns: 1fr;
    gap: 15px;
  }

  .stats-display {
    flex-direction: column;
    gap: 15px;
  }
}
